import { combineReducers } from 'redux'
import states from './statesInventory'
import initialState from './initialState'
import Cookie from 'universal-cookie'

const cookie = new Cookie();

export default combineReducers({
  apiProcess,
  isAuthenticated,
  allowedApplications,
  pnsdata
})

function apiProcess(state = initialState, action) {
  switch (action.type) {
    case states.onFormLoading:
      return Object.assign({}, state, {
        isLoading: {
          form : action.isLoading,
          app: {status: false, message: ''}
        }
      })
    case states.onAppLoading:
    return Object.assign({}, state, {
      isLoading: {
        form : {status: false, message: ''},
        app: action.isLoading
      }
    })
    case states.hasFormError:
      return Object.assign({}, state, {
        hasError: {
          form : action.hasError
        }
      })
    case states.isSuccess:
      return Object.assign({}, state, {
        isSuccess: action.isSuccess
      })
    default:
      return initialState.apiProcess
  }
}

function isAuthenticated (state = initialState, action) {
  switch (action.type) {
    case states.isAutheticated:
      return action.isAuthenticated;
    default:
      return localStorage.getItem('isAuthenticated') !== null && cookie.get('kemenkumhamssoengine_pns') != null && cookie.get('kemenkumhamssoengine') != null;
  }
}

function pnsdata (state = initialState, action) {
  switch (action.type) {
    case states.onchangepnsdata:
      return action.pnsdata
    default:
      return initialState.pnsdata;
  }
}


function allowedApplications (state = initialState, action) {
  switch (action.type) {
    case states.allowedApplications:
      return action.applications;
    default:
      return initialState.allowedApplications;
  }
}



