import Cookie from 'universal-cookie'
const cookie = new Cookie();


export default {
  isAuthenticated: (localStorage.getItem('isAuthenticated') !== null && cookie.get('kemenkumhamssoengine_pns') != null && cookie.get('kemenkumhamssoengine') != null),
  pnsdata: {
    justchange: false,
    data: cookie.get('kemenkumhamssoengine_pns') != null ? cookie.get('kemenkumhamssoengine_pns') : {},
  },
  allowedApplications: [],
  apiProcess: {
    isLoading: {
      form: {
        status: false,
        message: ''
      }, app: {
        status: false,
        message: ''
      }
    },
    hasError: {
      form: {
        status: false,
        message: '',
        key: ''
      }
    },
    isSuccess: {
      status: false,
      message: '',
      key: ''
    }
  }
}



