import React, {Component} from "react";
import { withRouter, Redirect } from 'react-router-dom'
import { connect } from 'react-redux';


const rendermessage = (message)=> {
  if (message == null) {
    return ('Anda tidak memiliki <b>authorisasi</b> pada layanan ini.');
  } else {
    return message;
  }
}


class Unauthorized extends Component {
  render() {
    return (
      <div className="row unauthorized">
        <div className="col-lg-9 col-sm-4 mb-3">
          <span style={{fontSize: 100, fontWeight: "bold"}}>Maaf,</span> <br />
          <span style={{fontSize: 40}}>{ rendermessage(this.props.message) }</span>
        </div>
        <div className="col-lg-3 col-sm-8 text-center mb-3">
          <img className="applicationimage rounded-circle d-block mx-auto" src={'img/unauthorized-logo.jpg' } width="180px" height="180px" style={{ marginTop: 50}}/>
        </div>
      </div>
    )
  }
}



export default withRouter(connect(null, null)(Unauthorized));



