import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';

const renderformcontrolclass = (isformcontrol, err, success)=> {
  let _formcontrolclass = '';
  if (isformcontrol) {
      _formcontrolclass += 'form-control ';
  }
  if (err.formerror.status && err.key === err.formerror.key) {
    _formcontrolclass += 'is-invalid ';
  }
  if (success != null) {
    if (success.formsuccess.status && success.key === success.formsuccess.key) {
      _formcontrolclass += 'is-valid ';
    }
  }
  return _formcontrolclass;
}

const renderinputform = (props)=> {
  return (
    <input type={ props.input.type } className={ renderformcontrolclass(props.input.isformcontrol, props.error, props.success) + props.input.class } id={ props.input.id } name={ props.input.name } placeholder={props.input.placeholder} onChange={ (props.events.onchange == null) ? '' : props.events.onchange }  value={props.input.existingvalue}/>
  );
}

const rendervalidationinfo = (err, success)=> {
  if (err.formerror.status && err.key === err.formerror.key) {
    return (
      <div className="invalid-feedback">
        { err.formerror.message }
      </div>
    );
  }
  if (success != null) {
    if (success.formsuccess.status && success.key === success.formsuccess.key) {
      return (
        <div className="valid-feedback">
        { success.formsuccess.message }
        </div>
      );
    }    
  }
  return null;
}

class InputFormValidation extends Component {
  render() {
    return (
      <div className="form-row">
        {renderinputform(this.props)}
        {rendervalidationinfo(this.props.error, this.props.success)}
      </div>
    );
  }
}

const mapStateToProps = (state)=> {
  return {
  }
}

const mapDispatchToProps = (dispatch)=> {
  return {
  }
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(InputFormValidation));



